Session.setDefault("currentUrl", {index: "active", login: "", reg: "",friend:"",lists:"",self:""});
Session.setDefault("info", {success:"",error:""});
Posts = new Meteor.Collection("posts");
SystemInfo = new Meteor.Collection("systemInfo");
UserInfo = new Meteor.Collection("userInfo");

Template.info.helpers({
    info(){
        return Session.get("info");
    },
});

Template.container.helpers({
    currentUrl(){
        return Session.get("currentUrl");
    },
});

Template.nav.helpers({
    active(){
        return Session.get("currentUrl");
    },
});

Template.index.helpers({
    topPosts(){
        return Posts.find({"top":true,"super":0},{sort: {time: -1}});
    },
    posts(){
        if(!Meteor.userId()){
            return Posts.find({"user._id":"NAEQJhrKe79xtC8Hd","super":0,"top":false,},{sort: {time: -1}});
        }else{
            return Posts.find({"super":0,"top":false},{sort: {time: -1}});
        }
    },
    comments(){
        return Posts.find({"super":this._id},{sort: {time: -1}});
    },
});

Template.lists.helpers({
    lists(){
        return UserInfo.find({},{sort: {rank: 1}})
    },
});

Template.self.helpers({
    self(){
        return UserInfo.find({"user._id":Meteor.userId()});
    },
});

Template.friend.helpers({
    friend(){
        var Afriend = UserInfo.findOne({"user._id":Meteor.userId()}).Friends;
        return UserInfo.find({"user._id":{$in : Afriend}},{sort:{rank:1}});
    },
});

Meteor.startup(function () {
    Backbone.history.start({pushState: true});
});

var urlRouter = Backbone.Router.extend({
    routes: {
        "": "index",
        "login": "login",
        "reg": "reg",
        "logout": "logout",
        "friend": "friend",
        "lists": "lists",
        "self": "self"
    },
    index: function () {
        Session.set("currentUrl", {index: "active", login: "", reg: "",friend:"",lists:"",self:""});
    },
    friend: function () {
        Session.set("currentUrl", {index: "", login: "", reg: "",friend:"active",lists:"",self:""});
    },
    lists: function () {
        Session.set("currentUrl", {index: "", login: "", reg: "",friend:"",lists:"active",self:""});
    },
    self: function () {
        Session.set("currentUrl", {index: "", login: "", reg: "",friend:"",lists:"",self:"active"});
    },
    login: function () {
        if(Meteor.userId()){
            this.navigate("/",true);
            Session.set("info", {success:"", error: "Áî®Êà∑Â∑≤Âú®Á∫øÔºÅ"});
        }
        Session.set("currentUrl", {index: "", login:"active", reg: "",friend:"",lists:"",self:""});
    },
    reg: function () {
        if(Meteor.userId()){
            this.navigate("/",true);
            Session.set("info", {success:"", error: "Áî®Êà∑Â∑≤Âú®Á∫øÔºÅ"});
        }
        Session.set("currentUrl", {index: "", login: "", reg: "active",friend:"",lists:"",self:""});
    },
    logout: function () {
        if(Meteor.userId()){
            Meteor.logout();
            this.navigate("/",true);
            Session.set("info", {success:"ÁôªÂá∫ÊàêÂäü", error: ""});
            SystemInfo.update({"_id":"1"},{$inc: {totalLogin:-1}});
        }else{
            this.navigate("/",true);
            Session.set("info", {success:"", error: "Áî®Êà∑‰∏çÂú®Á∫øÔºÅ"});
        }
    },
    redirect: function(url){
        this.navigate(url, true);
    }
});
Router = new urlRouter;

Template.reg.events({
    'click #submit': function (event) {
        event.preventDefault();
        if(!SystemInfo.find({"_id":"1"},{totalUser:true})['totalUser']){
            SystemInfo.insert({"_id":"1","totalUser":0,"totalPost":0,"totalLogin":0});
        }
        var $username = $("#username").val();
        var $password = $("#password").val();
        var $useremail = $("#useremail").val();
        var $password_repeat = $("#password-repeat").val();
        if ($password.length === 0 || $username.length === 0) {
            Session.set("info", {success: "", error:"Áî®Êà∑ÂêçÊàñËÄÖÂØÜÁ†Å‰∏çËÉΩ‰∏∫Á©∫"});
            return;
        }
        if ($password !== $password_repeat) {
            Session.set("info", {success: "",error:"‰∏§Ê¨°ËæìÂÖ•ÂØÜÁ†Å‰∏ç‰∏ÄËá¥"});
            return;
        }
        Accounts.createUser({
            username: $username,
            password: $password,
            email: $useremail},
            function(err){
                if(err){
                    Session.set("info", {success:"", error:"Ê≥®ÂÜåÂ§±Ë¥•"});
                }else{
                    Session.set("info", {success:"Ê≥®ÂÜåÊàêÂäü", error:""});
                    UserInfo.insert({
                        user:Meteor.user(),
                        isAdmin:false,
                        totalScore:0,
                        todayScore:0,
                        totalFriend:1,
                        Friends:[Meteor.userId()],
                        rank:SystemInfo.findOne({"_id":"1"},{totalUser:true})["totalUser"],
                        time:new Date()
                    });
                }
            }
        );
        SystemInfo.update({"_id":"1"},{$inc: {"totalUser":1}});
        SystemInfo.update({"_id":"1"},{$inc: {"totalLogin":1}});
    }
});

Template.login.events({
    'click #submit': function (event) {
        event.preventDefault();
        var $username = $("#username").val();
        var $password = $("#password").val();
        if ($password.length === 0 || $username.length === 0) {
            Session.set("info", {success:"", error: "Áî®Êà∑ÂêçÊàñÂØÜÁ†Å‰∏çËÉΩ‰∏∫Á©∫"});
            return;
        } 
        Meteor.loginWithPassword($username, $password, function (err) {
            if(err){
                Session.set("info", {success:"", error:"ÁôªÈôÜÂ§±Ë¥•ÔºÅÁî®Êà∑Âêç‰∏éÂØÜÁ†Å‰∏çÂåπÈÖç„ÄÇ"});
            }else{
                Router.redirect("/");
                SystemInfo.update({"_id":"1"},{$inc: {"totalLogin":1}});
                Session.set("info", {success: "ÁôªÈôÜÊàêÂäü", error:""});
            }
        });
    }
});

